@mixin flex($justify) {
    display: flex;
    flex-direction: column;
    justify-content: $justify;
    align-items: center; }


@mixin titleOffer() {
  & .title {
        position: relative;
        width: 100%;
        height: 20vh;
        text-align: center;
        line-height: 20vh;
        font-size: 3rem;
        font-family: sans-serif;
        text-transform: uppercase;
        color: black;
        overflow: hidden;
        &::before, &::after {
            content: attr(data-text);
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%; }
        &::before {
            left: 2px;
            text-shadow: -1px 0 red;
            background-color: white;
            clip: rect(10vh, 100vw, 9vh, 0);
            animation: glitch-anim 2s linear both infinite alternate-reverse;
            z-index: 1; }
        &::after {
            left: -2px;
            text-shadow: -1px 0 blue;
            background-color: white;
            clip: rect(10vh, 100vw, 9vh, 0);
            animation: glitch-anim 2s linear infinite  alternate;
            z-index: 1; } } }


/* animation for imgs about */

@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content; }
  @-moz-keyframes #{$name} {
    @content; }
  @-ms-keyframes #{$name} {
    @content; }
  @keyframes #{$name} {
    @content; } }

